// IMAGE ON HOVER SHOW DESCRIPTION
.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: rgba(22, 95, 78, 0.6);
}
.image.card-hover:hover .overlay {
  opacity: 1;
}
.description {
  color: rgb(255, 255, 255);
  font-weight: bold;
  margin: 2px;
  font-size: 19px;
  position: absolute;
  top: 50%;
  margin-left: 140px;
  width: 90%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  font-family: 'Quicksand';
}
.extra.content{
  font-family: 'Quicksand';
}
.post-field{
  display:none;
}

